@import-normalize;

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

:root {
  /******variable colors************/
  --color-01: #fffcf2;
  --color-02: #ccc5b9;
  --color-03: #403d39;
  --color-04: #252422;
  --color-05: #eb5e28;
  --bs-blue: #0d6efd;
  --bs-indigo: #6610f2;
  --bs-purple: #6f42c1;
  --bs-pink: #d63384;
  --bs-red: #dc3545;
  --bs-orange: #bd5d38;
  --bs-yellow: #ffc107;
  --bs-green: #198754;
  --bs-teal: #20c997;
  --bs-cyan: #0dcaf0;
  --bs-white: #fff;
  --bs-gray: #6c757d;
  --bs-gray-dark: #343a40;
  --bs-gray-100: #f8f9fa;
  --bs-gray-200: #e9ecef;
  --bs-gray-300: #dee2e6;
  --bs-gray-400: #ced4da;
  --bs-gray-500: #adb5bd;
  --bs-gray-600: #6c757d;
  --bs-gray-700: #495057;
  --bs-gray-800: #343a40;
  --bs-gray-900: #212529;
  --bs-primary: #bd5d38;
  --bs-secondary: #6c757d;
  --bs-success: #198754;
  --bs-info: #0dcaf0;
  --bs-warning: #ffc107;
  --bs-danger: #dc3545;
  --bs-light: #f8f9fa;
  --bs-dark: #212529;
  /***variables to elements*****/
  --border-menu: rgba(255, 255, 255, 0.1);
  --shadow-menu_color-link: rgba(255, 255, 255, 0.55);
  --color-hover-link: rgba(255, 255, 255, 0.75);
  --hover-switch: var(--color-05);
  --bg-hover-btn-hide-sidebar: var(--bs-gray-900);
  --bg-btn-show-sidebar: var(--bs-primary);
  /*colors palete theme dark*/
  --bg-dark: #000000;
  --text-primary: #f0f0f0;
  /*variables theme with values theme light*/
  /*general*/
  --text: var(--bs-gray-600);
  --bg-body: var(--bs-white);
  /*navbar component*/
  --bg-navbar_footer: var(--bs-primary);
  --text-emph: var(--bs-white);
  --bg-switch-theme: var(--bs-gray-dark);
  --bg-switch-controler: var(--bs-gray-500);
  --border-button-menu: var(--border-menu);
  --shadow-button-menu_color-link: var(--shadow-menu_color-link);
  --hover-link: var(--color-hover-link);
  --hover-switche-controler: var(--hover-switch);
  --color-icon-switch: var(--color-05);
  /*section about*/
  --text-title: var(--bs-gray-dark);
  --text-title-name-emph_date-betewen: rgba(189, 93, 56, 1);
  --link-email_instituicao: var(--bs-primary);
  --bg-link-social_link-curriculo_buttons-load: var(--bs-gray-700);
  --hover-link-social_link-curriculo_buttons-load: var(--bs-primary);
  --text-link-curriculo_buttons-load: var(--bs-white);
  --bg-diviser: var(--bs-gray-600);
  --link-hover-email_instituicao: #974a2d;
  /*card project*/
  --bg-preview-datas: rgb(0, 0, 0, 0.65);
  --border-color-project-link: #d7d9b1;
  --border-color-hover-project-link: #f5f8de;
  --hover-text-color-project-link: #fff8f0;
  --color-text-project-title_link: var(--bs-white);
  --color-project-link: var(--bs-gray-dark);
  --border-color-project-link: var(--bs-gray-600);
  --color-hover-project-link: var(--bs-gray-600);
  --bg-btns: var(--bs-gray-700);
  --text-btns: var(--bs-white);
  --bg-list-projects: var(--bs-gray-100);
  --bg-scrollbar: var(--bs-gray-300);
  --bg-thumb-scroll: var(--bs-gray-600);
  /*footer*/
  --text-color-footer: rgba(255, 255, 255, 0.5);
  --text-color-link-footer: var(--bs-gray-100);
  --text-color-hover-link-footer: var(--bs-gray-300);
  --text-tecnology: var(--bs-gray-600);
  /*other variables*/
  --shadow-btn-home: rgba(0, 0, 0, 0.2);
  --icon-home: var(--bs-orange);
  --icon-home-active: var(--bs-gray-100);
  --bg-btn-home-active: var(--bs-orange);
  --bg-color-select: var(--bs-orange);
  --bg-select: #f5f5f5;
  --border-color-select-input: var(--bs-gray-600);
  --box-shadow-focus: var(--bs-gray-300);
  --bg-select-combo-menu: #f5f5f5;
  --color-select-option-current: #0067b8;
  --bg-select-hover-option: rgb(0 0 0 / 10%);
  --border-menu-select: var(--bs-gray-700);
  --color-border-input: var(--bs-gray-400);
  --color-btn-icon-all-projects: var(--bs-orange);
  --bg-color-notification: var(--bs-gray-900);
  --color-text-notification: var(--color-05);
  --bg-btn-show-sidebar: var(--color-05);
}

html {
  font-size: 16px;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Saira Extra Condensed", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--text-title);
  line-height: 1.2;
}

.heading2 {
  font-size: calc(1.475rem + 2.7vw);
  margin-bottom: 3rem;
}

.heading3 {
  font-size: calc(1.325rem + 0.9vw);
}

.heading4 {
  font-weight: 500;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--text);
  line-height: 1.5;
}

.body {
  color: var(--text);
  background-color: var(--bg-body);
  position: relative;
}

.main {
  padding-top: 3.22rem;
  width: 100%;
}

@media screen and (min-width: 992px) {
  .main {
    padding: 0 0 0 17rem;
  }

  .main--pading {
    padding: 0;
  }
}

.has-dialog {
  overflow: hidden;
}

[data-theme="dark"] {
  --bg-body: var(--bg-dark);
  --text: var(--text-primary);
  --bg-navbar_footer: #31332e;
  --text-title: #d7dbdb;
  --text-title-name-emph_date-betewen: var(--color-05);
  --link-email_instituicao: var(--color-05);
  --link-hover-email_instituicao: var(--bs-gray-500);
  --bg-switch-theme: #878787;
  --bg-switch-controler: #31332e;
  --hover-switche-controler: #974a2d;
  --hover-link-social_link-curriculo_buttons-load: #974a2d;
  --color-project-link: #d7dbdb;
  --border-color-project-link: var(--color-05);
  --color-hover-project-link: var(--color-05);
  --text-tecnology: var(--bs-gray-500);
  --color-icon-switch: var(--bs-gray-800);
  --bg-btns: #31332e;
  --text-btns: var(--bs-gray-200);
  --bg-list-projects: #0a0b0d;
  --bg-scrollbar: var(--bs-gray-900);
  --shadow-btn-home: var(--color-05);
  --icon-home-active: var(--bs-gray-100);
  --bg-btn-home-active: #974a2d;
  --bg-select: transparent;
  --border-color-select-input: var(--bs-gray-300);
  --box-shadow-focus: var(--bs-gray-700);
  --bg-select-combo-menu: var(--bs-gray-900);
  --color-select-option-current: var(--color-05);
  --bg-select-hover-option: #974a2d;
  --border-menu-select: var(--bs-gray-200);
  --bg-color-notification: #974a2d;
  --color-text-notification: var(--bs-gray-300);
  --bg-hover-btn-hide-sidebar: var(--bs-primary);
}
